import Vue from 'vue'
import VueRouter from 'vue-router'
import App from '../App.vue'

import Chats from '../components/Chats.vue'
import Find from '../components/Find.vue'
import Friends from '../components/Friends.vue'
import More from '../components/More.vue'

import Profile from '../components/Profile.vue'
import Chating from '../components/Chating.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'App',
    component: App
  },
  { path: '/friends', component: Friends, name: 'friends' }, // 주소 이름이 됨
  { path: '/chats', component: Chats, name: 'chats' },
  { path: '/find', component: Find, name: 'find' },
  { path: '/more', component: More, name: 'more' },
  { path: '/profile', component: Profile, name: 'profile' },
  { path: '/chating', component: Chating, name: 'chating' }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: 'tab-bar__tab--selected', // 링크 생겼을 때
  linkExactActiveClass: 'tab-bar__tab' // 링크 벗어났을 때
})

export default router
